import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { HashRouter as Router, Route } from "react-router-dom";
import MainMenu from './MainMenu';
import DrawerMain from './DrawerMain';
import Dashboard from './Dashboard';
import AddPost from './AddPost';
import config from "../../config";

const styles = theme => ({
    root: {
        flexGrow: 1,
        height: 430,
        zIndex: 1,
        overflow: 'hidden',
        position: 'relative',
        display: 'flex',
        width: '100%',
    },
    navIconHide: {
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: `70px ${theme.spacing.unit * 3}px`,
    },
});

class Admin extends Component {
    constructor(props) {
        super(props)

        this.state = {
            drawerOpen: false
        }
        this.toggleDrawer = this.toggleDrawer.bind(this)
    }

    toggleDrawer() {
        this.setState({
            drawerOpen: !this.state.drawerOpen,
        });
    };

    render() {
        const { classes, theme } = this.props;
        const routes = [
            {
                path: "/",
                exact: true,
                menu: 'Dashboard',
                content: () => <Dashboard {...config}/>
            },
            {
                path: "/addpost",
                exact: true,
                menu: 'AddPost',
                content: () => <AddPost {...config}/>
            },
        ];

        return (
            <Router>
                <div className={classes.root}>
                    <MainMenu config={config} routes={routes} toggleDrawer={this.toggleDrawer}/>
                    <DrawerMain config={config} routes={routes} toggleDrawer={this.toggleDrawer} open={this.state.drawerOpen}/>

                    <main className={classes.content}>
                        {
                            routes.map((route, index) => (
                                <Route
                                    key={index}
                                    path={route.path}
                                    exact={route.exact}
                                    component={route.content}
                                />
                            ))
                        }
                    </main>
                </div>
            </Router>
        );
    }
}

Admin.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Admin);


