import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import {Link} from "react-router-dom";

const styles = {
    list: {
        width: 250,
    },
    fullList: {
        width: 'auto',
    },
};

class DrawerMain extends Component {

    render() {
        return (
            <SwipeableDrawer
                open={this.props.open}
                onClose={this.props.toggleDrawer}
                onOpen={this.props.toggleDrawer}
            >
                <div
                    tabIndex={0}
                    role="button"
                    onClick={this.props.toggleDrawer}
                    onKeyDown={this.props.toggleDrawer}
                >
                    <List component="nav" style={{width: 250}}>
                        <ListItem button component={Link} to="/">
                            <ListItemText primary="Home" />
                        </ListItem>
                        <ListItem button component={Link} to="/codes">
                            <ListItemText primary="Codes" />
                        </ListItem>
                        <ListItem button component={Link} to="/youtube">
                            <ListItemText primary="Youtube" />
                        </ListItem>
                    </List>
                </div>
            </SwipeableDrawer>
        );
    }
}

DrawerMain.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(DrawerMain);



